import * as THREE from 'three';
import { phyllotaxisWrong, phyllotaxisConical } from './phyllotaxis.js';
import Strategy from './strategy.js';
import {
	positionPetalsWrongPhyllotaxis,
	makePetalGeom,
	disposeTextures
} from './flowerModeller.js';

export default class Flower {
	constructor(params, materials, assets, birdType, maxAnisotropy){
		this.assets = assets;
		this.materials = materials;
		this.birdType = birdType;
		this.maxAnisotropy = maxAnisotropy;
		this.objects = [];
		this.group = new THREE.Group();
		this.strategy = new Strategy(materials);
		this._params = params;

		this.generate(params);
	}

	makePetalsVisible(opacity){
		for (let i = this.group.children.length - 1; i >= 0; i--){
			this.group.children[i].material.opacity = opacity;
		}
	}

	generate(params, debug = false){
		// containers per material
		const geometriesCrown = new THREE.Geometry();
		const geometriesOne = new THREE.Geometry();
		const geometriesTwo = new THREE.Geometry();
		const geometriesThree = new THREE.Geometry();
		const geometriesFour = new THREE.Geometry();

		const tot_petals = params.num;
		const PItoDeg = Math.PI / 180.0;
		const angleInRadians = params.angle * PItoDeg;

		const crownGeom = makePetalGeom(params, 'crown');
		const petalGeom = makePetalGeom(params, 'petals');
		const secPetalGeom = makePetalGeom(params, 'sec_petals');
	
		for (let i = 0; i < tot_petals; i++){
			const angle = params.angle;
			const strategy = this.strategy.get(i, angle, params, crownGeom, petalGeom, secPetalGeom);
			const opt = {
				params,
				geometry: strategy.geometry,
				materialKey: strategy.matkey
			};

			const object = new THREE.Object3D();
			positionPetalsWrongPhyllotaxis(i, params, object, angleInRadians);

			switch(opt.materialKey){
				case 'crown':	
					geometriesCrown.merge(opt.geometry, object.matrixWorld);
					break;
				case 'petal_one':
					geometriesOne.merge(opt.geometry, object.matrixWorld);
					break;
				case "petal_two":
					geometriesTwo.merge(opt.geometry, object.matrixWorld);
					break;
				case "petal_three":
					// last ring ha his own mat and geom. in between 3 mat and radius
					geometriesThree.merge(opt.geometry, object.matrixWorld);
					break;
				case "petal_four":
					// last ring ha his own mat and geom. in between 3 mat and radius
					geometriesFour.merge(opt.geometry, object.matrixWorld);
					break;
				default: // normal
					geometriesCrown.merge(opt.geometry, object.matrixWorld);
					break;
				}
		}
		

		if(geometriesCrown.vertices.length > 0){
			const MeshCrown = new THREE.Mesh(
				new THREE.BufferGeometry().fromGeometry(geometriesCrown),
				this.materials.crown
			);
			this.group.add(MeshCrown);
		}

		if(geometriesOne.vertices.length > 0){
			const MeshOne = new THREE.Mesh(
				new THREE.BufferGeometry().fromGeometry(geometriesOne),
				this.materials.petal_one
			);
			this.group.add(MeshOne);
		}

		if(geometriesTwo.vertices.length > 0){
			const MeshTwo = new THREE.Mesh(
				new THREE.BufferGeometry().fromGeometry(geometriesTwo),
				this.materials.petal_two
			);
			this.group.add(MeshTwo);
		}

		if(geometriesThree.vertices.length > 0){
			const MeshThree = new THREE.Mesh(
				new THREE.BufferGeometry().fromGeometry(geometriesThree),
				this.materials.petal_three
			);
			this.group.add(MeshThree);
		}

		if(geometriesFour.vertices.length > 0){
			const MeshFour = new THREE.Mesh(
				new THREE.BufferGeometry().fromGeometry(geometriesFour),
				this.materials.petal_four
			);
			this.group.add(MeshFour);
		}

		this.group.rotateY(-Math.PI / 2);
	}
}



