/* eslint-env browser */
const ParrotType = 'blue-fronted-parrot';
const BGCol = 0xE6A824;

// const ParrotType = 'budgeridgar';
// const BGCol = 0xFF97B4;

// const ParrotType = 'eastern-rosella';
// const BGCol = 0x0ECCCF;

// const ParrotType = 'ring-necked-parakeet';
// const BGCol = 0x164ACC;

// const ParrotType = 'fischers-lovebird';
// const BGCol = 0xFF5A35;

const debug = false;
const targetSize = 2048;

import * as THREE from 'three';
import Stats from 'stats.js';
import { loadBird } from './assets.js';
import { limitControls } from './utils.js';
import BufferManager from './buffersManager.js';

import { createRevolver } from './flowers/factory';
import { PointLights } from './pointLights.js';

//setup scene and camera
const scene = new THREE.Scene();
const distanceElementsFromCenter = 200;

const OrbitControls = require('three-orbit-controls')(THREE);
const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 2000);
const renderer = new THREE.WebGLRenderer({ antialias: true, transparent: true });
renderer.setSize(window.innerWidth, window.innerHeight);
renderer.setClearColor(BGCol);

//const gl = renderer.getContext();
//gl.enable(gl.SAMPLE_ALPHA_TO_COVERAGE);
const maxAnisotropy = renderer.capabilities.getMaxAnisotropy();
let controls;

// init variables
let time;
let buffers;
let revolver;
let revolverInFBO;
const clock = new THREE.Clock();
const stats = new Stats();

function init(assets){
	document.body.appendChild(renderer.domElement);
	camera.position.z = 200;
	camera.position.y = -50;

	// stats
	stats.showPanel(0); // 0: fps, 1: ms, 2: mb, 3+: custom
	window.addEventListener('resize', () => {
		let WIDTH = window.innerWidth,
			HEIGHT = window.innerHeight;
		renderer.setSize(WIDTH, HEIGHT);
		camera.aspect = WIDTH / HEIGHT;
		camera.updateProjectionMatrix();
	});

	if (debug){
		document.body.appendChild(stats.domElement);
		const axisHelper = new THREE.AxesHelper(50);
		scene.add(axisHelper);
	}

	revolver = createRevolver(ParrotType, assets, maxAnisotropy);

	buffers = new BufferManager(targetSize, -distanceElementsFromCenter);

	controls = new OrbitControls(camera, renderer.domElement);
	limitControls(controls);

	const distance = distanceElementsFromCenter;
	const power = 1.0;
	PointLights(distance, power).map(light => {
		scene.add(light);
	});
	PointLights(distance, power).map(light => {
		buffers.getBufferScene().add(light);
	});

	// add revolver clone to the FBO
	revolverInFBO = revolver.group.clone();
	buffers.getBufferScene().add(revolverInFBO);

	// add plane and revolver to the scene
	scene.add(buffers.getPlane());
	revolver.group.position.set(0,0,70);
	scene.add(revolver.group);
	render();
}

function render(){
	time = clock.getElapsedTime();
	stats.begin();
	let sinedT = Math.sin(time * 0.2);
	let rot = Math.pow(sinedT, 2.0) * Math.sign(sinedT) * 0.02;
	revolver.group.rotateZ(rot);
	revolverInFBO.rotateZ(rot);


	buffers.update(time);
	renderer.render(buffers.getBufferScene(), camera, buffers.getTextureB(), true);

	renderer.render(scene, camera);
	stats.end();
	requestAnimationFrame(render);
}

function removeSpinner(){
	const elem = document.getElementsByClassName('loading')[0];
	if (elem){
		elem.parentNode.removeChild(elem);
	}
}

loadBird(ParrotType)
	.then(
		assets => {
			removeSpinner();
			init(assets);
		},
		err => {
			console.log(`impossible to load the assets: ${err}`);
		}
	)
	['catch'](error => {
		console.error(error.stack);
	});



